import { Button } from '@mui/material';
import React, { useState, useEffect } from 'react';

export default function CButton({ playercaptions, setscore, user ,setsubmit}) {
  const [selected, setselected] = useState(false);

  const handleClick = () => {

    setselected(prevSelected => !prevSelected); // Toggle the 'selected' state
  };

  useEffect(() => {
    if (selected===true){
        setscore(user,4);
    }
    else{
        setscore(user,0);
    }
  }, [selected]);

  return (
    <Button
      onClick={handleClick}
      variant="text"
      id="caprate"
      style={{
        textAlign: "center",
        width: "100%",
        color: "white",

        background: selected ? "#FFE130" : "white",
        fontFamily: "Impact",
        borderRadius: "10px",
        padding: "15px",
      }}
    >
      {playercaptions}
    </Button>
  );
}
