import { Grid, Stack, Button } from '@mui/material'
import React from 'react'
import CButton from './CButton'

export default function Rating({img,playerlist,playercaptions,userid,setscore,sendscore,roomId,ep}) {
  
  return (
    <Stack alignItems="center" spacing={3} style={{marginTop:"100px",fontSize:"20px",color:"white",fontFamily:"Impact"}}>
    <video style={{borderRadius:'7px',background:'black'}} src={img} loop={true} id='gifbox' autoPlay={true} />
    
   
        <center>
        <p id='cap' className='stfc'>SELECT THE FUNNIEST CAPTIONS 😹
        <Button onClick={()=>{sendscore(playerlist,roomId)}} variant="text" id="cap" className='rate' style={{marginLeft:"20px",textAlign:"center",width:"120px",letterSpacing:"1.5px",color:"white",lineHeight:"45px",fontSize:"23px",background:"white",fontFamily:"Impact",borderRadius:"24px",padding:"2px"}}>
            SUBMIT!
        </Button>
        </p>
        </center>
        
    
        
   
    
    <Grid container spacing={2} justifyContent={"center"} style={{width:"100%"}}>
        
        {playerlist? (
          <>
            {Object.entries(playerlist).map(([key, item]) => (
                <React.Fragment key={key}>
                {key !== 'gamestate' ? (
                <>  
                {key!==userid?
                <>
                {playercaptions&&playercaptions[key] && playercaptions[key].cap && playercaptions[key].cap[ep] !== undefined && playercaptions[key].cap[ep] !== "" && playercaptions[key].cap[ep] !== null?
                    
                    <Grid item xs={6} lg={5} style={{padding:"6px"}}>
                    
                        <CButton playercaptions={playercaptions[key].cap[ep]} setscore={setscore} user={key}>
                      
                        </CButton>
                    </Grid>
                :""}
                </>
                :""}
                </>
            ) : null}
                </React.Fragment>
            ))}
         </>
        ) : (
        <p>No data available.</p>
      )}
    </Grid>
     
    </Stack>
  )
}
