import { Avatar, Box, Button, Stack } from '@mui/material'
import React from 'react'
import { motion } from 'framer-motion';

export default function EndScore({scores,start,h,maxep,maxtime}) {
    const animationVariants = {
        hidden: {
          opacity: 0,
          y: 50, 
        },
        visible: {
          opacity: 1,
          y: 0, 
        },
      };
    

  return (
    <center>
    <motion.div
      initial="hidden"
      animate="visible"
      variants={animationVariants} 
      transition={{ duration: 1 }} >
    <Stack direction={"row"} justifyContent={"center"}>
    {scores[1]?<Avatar alt="name" src={"Faces/jpeg/"+scores[1][1].uinfo.face+".jpg"} sx={{position:"relative",top:"165px",left:"9px",zIndex:3,width: 80, height: 80,border:"10px solid white" }} variant="round" name="face"/>:<></>}    
    {scores[0]?<Avatar alt="name" src={"Faces/jpeg/"+scores[0][1].uinfo.face+".jpg"} sx={{position:"relative",top:"90px",left:"-6px", width: 150, height: 150,border:"10px solid white" }} variant="round" name="face"/>:<></>}
    {scores[2]?<Avatar alt="name" src={"Faces/jpeg/"+scores[2][1].uinfo.face+".jpg"} sx={{position:"relative",top:"180px",left:"-20px", width: 50, height: 50,border:"10px solid white" }} variant="round" name="face"/>:<></>}

    </Stack>    
        <Stack p={2} spacing={3} style={{background:"white", paddingTop:"50px",width:"400px",marginTop:"50px",borderRadius:"20px"}}>

        {scores.map(([key, item],index) => (
         
            <Stack direction="row" key={key} spacing={2} justifyContent="flex-start" alignContent={"flex-start"} id="score">
            <Box>
                <h4 style={{marginTop:"12px"}}>#{index+1}</h4>
            </Box>
            <Box>
            <Avatar
               alt="name"
               src={"Faces/jpeg/"+item.uinfo.face+".jpg"}
               sx={{ width: 50, height: 50 ,borderRadius:"10px"}}
               variant="rounded"
               name="face"
               />
             </Box>      
            <Box style={{marginRight:"30px",width:"600px"}}>
                <h4 style={{marginTop:"12px"}}>{item.uinfo.name}</h4>
                </Box> 
            <Box style={{marginLeft:"90px"}}><h4 style={{marginTop:"12px",width:"50px"}}>{item.score} 😹</h4></Box>
            </Stack>
         ))}
        
    </Stack>
    {h===true?
    <Button onClick={()=>{start(maxep,maxtime)}} variant="text" id="cap" className='rate' style={{marginTop:"40px",marginLeft:"20px",textAlign:"center",width:"120px",letterSpacing:"1.5px",color:"white",lineHeight:"45px",fontSize:"19px",background:"white",fontFamily:"Impact",borderRadius:"24px",padding:"2px"}}>
            Rematch
      </Button>
      :""}
    </motion.div>
    </center>
  )
}
