import { Avatar, Badge, Box, Paper, Stack, Tooltip, styled } from '@mui/material'
import React, { useEffect, useState } from 'react'

const tooltipstyle={
  tooltip: {
    sx: {
      background:"white",
      color:'black'
    },
  },
  arrow: {
    sx: {
    color: 'white',}
  }
}
const Item = styled(Paper)(({ theme }) => ({
    backgroundColor:'transparent',
  
    padding: theme.spacing(1),
    textAlign: 'center',
    borderRadius:"0px",
    
    border:"none",
    color: theme.palette.text.secondary,
  }));

const badgeStyle = {
    "& .MuiBadge-badge": {
      transform: 'translate(-6px, 44px)',
      width:"40px",
      height:"18px",
      background:"#D6B41B",
      fontSize:"10px",
      borderRadius:"7px"
    }
  }

  
export default function Friends({playerlist,userid}) {
  
  return (
    <Stack id="friendsbox" spacing={1} p={0.2} style={{paddingBottom:"10px",borderRadius:"0px 20px 20px 0px",
    border:"2px solid white",
    boxShadow: "rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px",maxHeight:"300px",overflow:"scroll",overflowX:"hidden",background:"white"}}>
        
        {playerlist? (
          <>
          {playerlist.map(([key, item]) => (
          <React.Fragment key={key}>
          {key !== 'gamestate' ? (
            <Tooltip title={(key===userid?"Me: ":"")+item.uinfo.name} placement="top-end" componentsProps={tooltipstyle} arrow>
          <Item elevation={0} id='frienditem'>        
               <Badge badgeContent={"😹 "+item.score} color="success" sx={badgeStyle}>
               <Avatar
               alt="name"
               src={`Faces/jpeg/${item.uinfo.face}.jpg`}
               sx={{ width: 50, height: 50 ,border:item.ishost===true?"2px solid #00FF47":(key===userid?"2px solid #AD00FF":"none")}}
               variant="round"
               name="face"
               />
              </Badge>
           
          </Item>
           </Tooltip>
          ) : null}
          </React.Fragment>
         ))}
       </>
      ) : (
        <p>No data available.</p>
      )}
      </Stack>
  )
}
