import { Stack } from '@mui/material'
import React from 'react'

export default function Waiting() {
  return (
    <center style={{position:"relative",top:"80px"}}>
    <Stack spacing={3} p={4} sx={{paddingBottom:"20px",width:"350px",}}>
    <img src="giphy.webp" alt="" />
    <p id='cap' style={{fontSize:"30px",textAlign:"center",letterSpacing:"1.6px",textTransform:"uppercase"}}>Waiting for Admin..</p>
    </Stack>
    </center>
  )
}
