import React from 'react'

export default function Time({time}) {
  return (
    <div style={{textAlign:"center",position:"fixed",color:"white",borderRadius:"25px",top:"-7px",right:"-7px",width:"50px",height:"50px",background:"#F0CF61",fontFamily:"Impact"}}>
      <h2 id='time' style={{marginTop:"11px"}}>{time}</h2>
    </div>
  )
}
