import { FormControl, IconButton, InputLabel, MenuItem, Select, Stack } from '@mui/material'
import React from 'react'

const inputstyle={color:"black",borderRadius:"12px", padding:"0px", height:"50px", boxShadow:"3px 3px 0px 0px rgba(0,0,0,1)"}
const startbtn={background:"red",color:"white",width:"50px",height:"50px",borderRadius:"50%",border:"none",boxShadow: "rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px"}
export default function Admin({setMaxep,setMaxtime,start,maxep,maxtime,roomId}){
  return (
   
    <center id="adminbox">
    <Stack spacing={3} p={4} sx={{paddingBottom:"20px",borderRadius:"20px",width:"300px",boxShadow: "rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px",background:"white"}}>
    
    <FormControl>
    <InputLabel style={{color:"#999999"}}>ROUNDS</InputLabel>
    <Select
    defaultValue=""
    onChange={(e)=>setMaxep(e.target.value)}
    style={inputstyle}
    label="ROUNDS">
          <MenuItem value={2}>2</MenuItem>
          <MenuItem value={5}>5</MenuItem>
          <MenuItem value={8}>8</MenuItem>
          <MenuItem value={10}>10</MenuItem>
    </Select>
    </FormControl>

    <FormControl>
    <InputLabel style={{color:"#999999"}}>CAPTION TIME</InputLabel>
    <Select
    defaultValue=""
    onChange={(e)=>setMaxtime(e.target.value)}
    style={inputstyle}
    label="CAPTION TIME">
          <MenuItem value={20}>20</MenuItem>
          <MenuItem value={30}>30</MenuItem>
          <MenuItem value={40}>40</MenuItem>
          <MenuItem value={60}>60</MenuItem>
        
    </Select>
    </FormControl>

   <center>
    
    <IconButton size="lg" onClick={()=>start(maxep,maxtime,roomId)}><div style={startbtn} ></div></IconButton>
    </center>
    
    </Stack>
    </center>
    
  )
}
