import { Stack } from '@mui/material'
import React from 'react'

export default function Messages({msg,setMsg}) {
  
  return (
   
    <div id='messages' style={{zIndex:5,fontFamily:"Impact",textTransform:"uppercase",color:"white",padding:"10px",borderRadius:"7px",position:"fixed", width:"300px",bottom:"20px",right:"20px", background:"white"}}>
    <div style={{fontSize:"0px"}}>{setTimeout(()=>{
      setMsg(false);
    },20000)}
    </div>
    <Stack spacing={3}>
        {msg==="You need atleast 2 friends"?<img src="callfriends.gif" alt="" />:""}
        {msg==="Room is Full"?<img src="giphy (2).gif" alt="" />:""}
        {msg==="Room Not Found"?<img src="it-doesnt-exist-not-existing.gif" alt="" />:""}
        
        <center style={{letterSpacing:"1px"}}>{msg}</center>
    </Stack>
    </div>
   
  )
}
