import { Stack } from '@mui/material'
import React from 'react'

export default function WaitRate() {
  return (
    <Stack alignItems="center" spacing={3} style={{marginTop:"100px",zIndex:100}}>
    <img style={{borderRadius:'7px',background:'black'}} src={"giphy (1).gif"} height="270" autoPlay={true} />
    <p id='cap' style={{fontSize:"30px",textAlign:"center",letterSpacing:"1.6px",textTransform:"uppercase"}}>Please wait your Friends are rating your caption..</p>
    </Stack>
  )
}
