import { Stack } from '@mui/material'
import React from 'react'

export default function Wait() {
  return (
    <Stack alignItems="center" spacing={3} style={{marginTop:"100px",zIndex:100}}>
    <img style={{borderRadius:'7px',background:'black'}} src={"giphy.gif"} width="480" height="270" autoPlay={true} />
    <p id='cap' style={{fontSize:"30px",textAlign:"center",letterSpacing:"1.6px",textTransform:"uppercase"}}>Hold Tight for your Friends..</p>
    </Stack>
  )
}
