import { Avatar, Box, Stack } from '@mui/material'
import React from 'react'
import { motion } from 'framer-motion';

export default function Score({scores}) {
    const animationVariants = {
        hidden: {
          opacity: 0,
          y: 50, 
        },
        visible: {
          opacity: 1,
          y: 0, 
        },
      };
      console.log("BrO",scores)
  return (
    <center>
    <motion.div
      initial="hidden" // Set the initial state to hidden
      animate="visible" // Set the final state to visible
      variants={animationVariants} // Variants defining the animation
      transition={{ duration: 1 }} // Animation duration
    >    
    <Stack p={2} spacing={3} style={{background:"white",width:"400px",marginTop:"100px",borderRadius:"20px"}}>
        <center style={{fontSize:"12px",letterSpacing:"6px"}}>LEADERBOARD</center>
        {scores.map(([key, item],index) => (
             
            <Stack direction="row" key={key} spacing={2} justifyContent="flex-start" alignContent={"flex-start"} id="score">
         
            <Box>
                <h4 style={{marginTop:"12px"}}>#{index+1}</h4>
            </Box>
            <Box>
            <Avatar
               alt="name"
               src={"Faces/jpeg/"+item.uinfo.face+".jpg"}
               sx={{ width: 50, height: 50 ,borderRadius:"10px"}}
               variant="rounded"
               name="face"
               />
             </Box>      
            <Box style={{marginRight:"30px",width:"600px"}}>
                <h4 style={{marginTop:"12px"}}>{item.uinfo.name}</h4>
                </Box> 
            <Box style={{marginLeft:"90px"}}><h4 style={{marginTop:"12px",width:"50px"}}>{item.score} 😹</h4></Box>
            </Stack>
         ))}
        
    </Stack>
    </motion.div>
    </center>
  )
}
