import { Box, Button, Stack } from '@mui/material'
import React from 'react'

import Textarea from '@mui/joy/Textarea';

export default function GifnTextBox({img,setCap,sendcaption,userid,roomId,cap,ep}) {
  return (
    <>
    <Stack alignItems="center" spacing={3} style={{marginTop:"100px"}}>
    <video style={{borderRadius:'2px',background:'black',boxShadow:"rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px"}} src={img} loop={true} id='gifbox' autoPlay={true} />
    <Box id="captxtarea">
    <Textarea name="caption" onChange={(e)=>setCap(e.target.value)} 
    style={{textAlign:"center",color:"white",borderRadius:"20px",fontSize:"30px",fontFamily:"Impact", padding:'12px', resize:"none",border:"none",boxShadow: "rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px"}}
    endDecorator={
      <Button id="time" sx={{ ml: 'auto' }} onClick={()=>sendcaption(userid,roomId,cap,ep)} variant="text" style={{textAlign:"center",width:"80px",boxShadow: "rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px",letterSpacing:"1.5px",color:"white",fontSize:"13px",background:"black",fontFamily:"Impact",borderRadius:"4px",padding:"2px"}}>
            Send!
        </Button>
    } variant="filled" id='cap' />
    </Box>

    
    </Stack>
    
    </>
  )
}
