import React, { useEffect, useState } from 'react';
import { redirect, useParams } from 'react-router-dom';
import io from 'socket.io-client';
import copy from "copy-to-clipboard";
import ContentCopyIcon from '@mui/icons-material/ContentCopy';

import { Box, IconButton, InputBase} from '@mui/material';

import Friends from './Friends/Friends';
import Admin from './PreGame/Admin';
import Waiting from './PreGame/Waiting';
import Time from './OnGame/Time';
import Round from './OnGame/Round';
import GifnTextBox from './OnGame/GifnTextBox';
import Wait from './OnGame/Wait';
import Rating from './OnRating/Rating';
import WaitRate from './OnRating/WaitRate';
import Score from './Scores/Score';
import EndScore from './Scores/EndScore';
import Messages from './PreGame/Messages';

import { Local,Production } from '../Api'
const Api = Production

export default function GameRoom (props) {
  const [socket, setSocket] = useState(null);
  const [playerlist, setPlayerList] = useState([]);
  const [host, setHost] = useState("");
  const [h,seth] = useState("");

  const [time, settime] = useState(null);
  
  const [wait,setwait] = useState(false);

  const [img, setimg] = useState(null)
  const [cap, setCap] = useState(null);

  const [playercaptions,setPlayercaptions] = useState(null);

  const [scores, setScores]=useState([]);

  const [gamestart, setGameStart] = useState(false)
  const [gamestate, setGamestate] = useState("notstarted")
  const [msg,setMsg] = useState(false);

  const [curep,setCurep] = useState(0);
  const [isset,setIsset] = useState(false)
  const [maxep, setMaxep] = useState(8);
  const [maxtime, setMaxtime] = useState(30);

  const { id } = useParams();
  const roomId = id || props.id;

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  
  
  useEffect(() => {//For Host
    if (socket) {
      socket.emit('gamestate', {room:roomId});
    }
  }, [socket]);
  
  useEffect(() => {//For Host
    if (props.id) {
      setHost(true);
    }
    else{
      setHost(false);
    }
  }, [props.id]);

  useEffect(() => {
    // Connect to the Socket.io server

      var newSocket = io('ws://'+Api.ws);
 
    // Handle Socket.io events
    newSocket.on('connect', () => {
      console.log(newSocket.id)
      console.log('Connected to Socket.io server');
      newSocket.emit('join', {room:roomId,user:props.userid,uinfo:props.udata,ishost:host});
    });

    newSocket.on('gamestepcaptioning', (data) => {
      console.log("hoebsbfsfdfsd");
      if (data!==null){
        setGameStart(true);
        console.log("imggg",data.img)
        setimg(data.img);
        setGamestate("showingimg")
      }
    });

    newSocket.on('gamesteprating', (data) => {
      setwait(false);
      if (data!==null){
        setGameStart(true);
        console.log("Problem",data.data)
        console.log("Problem Players",playerlist)
        setPlayercaptions(data.data)
        setGamestate("ratingimg")
      }
    });

    newSocket.on('endgamestep', (data) => {
      setwait(false);
      settime(null);
      const leaderboardEntries = Object.entries(data.data);
      leaderboardEntries.sort((a, b) => b[1].score - a[1].score);
      setScores(leaderboardEntries);
      setCurep(data.ep);
      setGamestate("endgamestep");
    });

    newSocket.on('endgame', (data) => {
    setwait(false);
    settime(null);
    
    const leaderboardEntries = Object.entries(data.data);
    leaderboardEntries.sort((a, b) => b[1].score - a[1].score);
    setScores(leaderboardEntries);
    setCurep(data.ep);
    setGamestate("endgame");  
    });

    newSocket.on('gamestate', (data) => {
      if (data.state!=="notstarted"){
        setGameStart(true);
        setMaxtime(data.maxtime);
        setMaxep(data.maxep);
        setCurep(data.curep);
        setIsset(true);
        setimg(data.curimg)
      }
    });

    newSocket.on('time', (data) => {
        settime(data);
    });

    newSocket.on('none', (data) => {
      console.log(data);
  });

    newSocket.on('start', (data) => {
      setMsg(false);
      setGameStart(data.start);
      setMaxtime(data.maxtime);
      setMaxep(data.maxep);
      setCurep(data.curep);
      setIsset(true)
    });

    newSocket.on('playerlistcheck', (data) => {
      console.log(data);
      if (data&&data[props.userid]){
        seth(data[props.userid].ishost);
       
      }
      else{

        seth(false);
      }
      
      if (data.gamestate){
        delete data.gamestate
      }
      const leaderboardEntries = Object.entries(data);
      leaderboardEntries.sort((a, b) => b[1].score - a[1].score);
      console.log("look",leaderboardEntries)
      setScores(leaderboardEntries);
      setPlayerList(data);
    });

    setSocket(newSocket);

    return () => {
    
      newSocket.disconnect();
      
    };
  }, [id, props,roomId,host]);

  const start = (maxep,maxtime,room=roomId) => {
    if (socket) {
      if(scores.length>=3){
        setIsset(true);
        socket.emit('start', {maxep:maxep,room:room,maxtime:maxtime});
      }
      else{
       setMsg("You need atleast 2 friends");
      }
    }
  };

  const sendcaption = (u,r,c,e) => {
    setGamestate("waiting");
    setCap(null)
    setwait("wc");
    if (socket) {
      socket.emit('caption', {user:u,room:r,caption:c,ep:e});
    }
  };
  

  const sendscore = (p,r) => {
    setGamestate("waitingr");
    setwait("wr");
    if (socket) {
      socket.emit('sendscore', {p:p,u:props.userid,r:r});
    }
  };

  const setscore = (key,score) => {
    setPlayerList((prevplayerlist) => ({
      ...prevplayerlist,
      [key]: {
        ...prevplayerlist[key],
        "score": score
      }
    }))
   
  };
  

  return (
    <>
    {msg!==false?<Messages msg={msg} setMsg={setMsg}/>:<></>}
    
    {gamestate!=="notstarted" && curep?
    <Round round={curep.toString()+"/"+maxep.toString()}></Round>
    :""}


    
    {time!==null?<Time time={time.toString()}/>:""}


    {img!==null && gamestate==="showingimg"? 
    <GifnTextBox img={img} setCap={setCap} ep={curep} sendcaption={sendcaption} userid={props.userid} roomId={roomId} cap={cap}/>
    :
    ""
    }

    {wait==="wc"?<Wait/>:""}
    {wait==="wr"?<WaitRate/>:""}

    {scores!==null && gamestate==="endgame"?
    <>
    <EndScore scores={scores} start={start} h={h} maxep={maxep} maxtime={maxtime}/>
    </>
    :""}
 

    {scores!==null && gamestate==="endgamestep"?
    <>
     <Score scores={scores}/>
     
    </>
    :""}


   {playercaptions!==null && gamestate==="ratingimg"? 
    <Rating roomId={roomId} ep={curep} sendscore={sendscore} img={img} playerlist={playerlist} playercaptions={playercaptions} userid={props.userid} setscore={setscore}/>
    :
    ""
    }
    
    {h===false?<>
      {gamestart===false?<Waiting/>:""}
    </>:""}
    
  
    {props.id && gamestart===false?<IconButton variant="contained" onClick={()=>copy(Api.client+roomId)} size="xs" style={{position:"fixed",bottom: "10px",right:"10px",background:"white",color:"black",zIndex:7,}}><ContentCopyIcon /></IconButton>:""}

     <Friends playerlist={scores} userid={props.userid}/>
     
     {host===true && isset===false || h===true && isset===false?
     <Admin setMaxep={setMaxep} setMaxtime={setMaxtime} start={start} maxep={maxep} maxtime={maxtime} roomId={roomId}/>
      :""}

    <div id='ghostbox'></div>

    </>
  );
}
